import { Component, h, Prop } from '@stencil/core';

@Component({
	tag: 'ks-logo-banner',
	styleUrl: 'logo-banner.css'
})
export class LogoBanner {

	@Prop() name: string;
	@Prop() image: string;
	@Prop() webp: string;
	@Prop() link: string;

	@Prop() width: number;
	@Prop() height: number;

	render() {
		return <a target="_blank" href={this.link ? this.link : null}>
            <ks-img2
				src={this.image}
				webp={this.webp}
				alt={this.name}
				width={this.width}
				height={this.height}
            ></ks-img2>
        </a>;
	}
}